<template>
  <div>
      <p class="text-purple-600">Welcome {{name}}</p>

      Name: {{name}}
      Score: {{ score }}


  <div class="flex mb-4">
    <div class="w-3/4 m-20">
    <div>
      <p class="text-7xl"> {{ first_element }} {{ operation }} {{ second_element }} </p>
    <div class="m-20">
      <div class="flex justify-center space-x-4">
        <div v-on:click="submit(number)" v-for="(number, index) in solutions" :key=index class="text-4xl bg-green-400	h-16 w-20 align-baseline">{{ number }} </div>
      </div>
    </div>
    </div>
    </div>
    <div class="w-1/4">
      Config
    </div>

  </div>
  </div>  

</template>


<script>
export default {
  props: {
    name: String,
  },
  data() { 
    return {
    first_element: 2,
    operation: "X",
    second_element: 4,
    solutions: [4,3,8,20],
    score:0,
    operations: ["x"],
    tafels: [3,10],
    solutions_size: 4
  }
  },
  methods: {
    generateNew: function() {
      this.second_element = this.tafels[Math.floor(Math.random() * this.tafels.length)];
      this.first_element = Math.floor(Math.random()*10)+1
      let solutions = []
      let new_element = 0
      for (let i=0;i<this.solutions_size;i++){
        do {
          new_element = Math.floor(Math.random()*10)+1
        }
        while(new_element == this.first_element)
        solutions.push(new_element * this.second_element)
      }
      let index_to_replace = Math.floor(Math.random() * solutions.length)
      solutions[index_to_replace]  = this.second_element * this.first_element
      this.solutions = solutions
    },
    submit: function(number) {
      if(this.operation == 'X') {
        if (this.second_element * this.first_element == number) {
          this.score = this.score + 10
        }
      }
      else if(this.operation == '+') {
          if (this.second_element * this.first_element == number) {
          this.score = this.score + 10
        }
      }
      this.generateNew()      
    }
  }
}
</script>